<template>
  <div class="app-page refund-list">
      <page-content class="page-content">
        <scroller style=""
                  :on-refresh="refresh"
                  :on-infinite="infinite"
                  ref="myscroller">
          <div class="list">
            <div class="item" v-for="(item, index) in list" :key="index">
              <div class="item-1">
                <div class="item-1-left">
                  <img src="/static/images/personal/pu.png"/>
                  <span>普通订单</span>
                </div>
                <div class="item-1-right">
                  <span>退款中</span>
                </div>
              </div>
              <div class="item-2">
                <div class="item-info">
                  <div class="info-img">
                    <img src="/static/images/category/jz.png"/>
                  </div>
                  <div class="info-detail">
                    <div class="info-detail-title">橙子橙子天然维C超甜橙 子橙子天然维C超甜</div>
                    <div class="info-detail-specification">规格:&nbsp;3kg</div>
                  </div>
                  <div class="info-num">
                    <div class="info-num-price">¥&nbsp;36.00</div>
                    <div class="info-num-quantity">x1</div>
                  </div>
                </div>
              </div>
              <div class="item-3">
                <p>共1件商品 合计: ¥</p>
                <p>50.00</p>
              </div>
              <div class="item-4">
                <button>取消退款</button>
                <button :class="{'button-active': true}">查看进度</button>
                <button @click="toRefundDetail">查看详情</button>
              </div>
            </div>
          </div>
        </scroller>
      </page-content>
  </div>
</template>
<script>
import PageContent from '@/components/page/content'
export default {
  name: 'refundList',
  components: { PageContent },
  data () {
    return {
      list: [1, 1, 1, 1, 1]
    }
  },
  methods: {
    /** 下拉刷新相关方法****/
    refresh (done) {
      console.log('下拉刷新')
      setTimeout(() => {
        this.list = [1, 1, 1, 1, 1]
        done()
      }, 1000)
    },
    /** 滚动加载相关方法****/
    infinite (done) {
      // 异步更新数据
      setTimeout(() => {
        console.log('滚动触发...')
        if (this.list.length >= 10) {
          done(true)
          this.$refs.myscroller.finishInfinite(true)
          return
        } else {
          this.list = this.list.concat([1, 1])
        }
        done()
      }, 1000)
    },
    // 跳转至退款详情
    toRefundDetail () {
      this.$router.push({ path: '/refund/detail', query: {} })
    }
  },
  created () {
  }
}

</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  @import '@/styles/variables.scss';
  .refund-list{
    font-family: $font-family-Medium!important;
    .page-content{
      position: relative;
      .list{
        padding: 30px 50px;
        >.item+.item{
          margin-top: 90px;
        }
        .item{
          .item-1{
            display: flex;justify-content: space-between;
            .item-1-left{
              flex: 1;
              height: 26px;
              img{width: 26px;height: 26px;}
              font-size: 26px;color: $black333333;
            }
            .item-1-right{
              font-size: 26px;color: $green22ac38;
            }
          }
          .item-2{
            background: #fff;
            width: 100%;height: 150px;
            display: flex;flex-direction: column;justify-content: space-between;
            margin-top: 40px;
            .item-info{
              width: 100%;height: 150px;
              display: flex;align-items: center;justify-content: space-between;
              .info-img{
                width: 150px;height: 100%;
                img{width: 100%;height: 100%;}
              }
              .info-detail{
                flex: 3;height: 100%;
                padding-left: 50px;
                .info-detail-title{
                  font-size: 29px;
                  color: $black333333;
                  font-weight: 600;
                }
                .info-detail-specification{
                  margin-top: 20px;
                  font-size: 24px;
                  color: $black999999;
                }
              }
              .info-num{
                flex: 1;height: 100%;
                .info-num-price{
                  display: flex;flex-direction: row-reverse;
                  font-weight: 600;
                  color: $black333333;
                  font-size: 28px;
                }
                .info-num-quantity {
                  display: flex;flex-direction: row-reverse;
                  margin-top: 10px;
                  font-size: 24px;
                  color: $black999999;
                }
              }
            }
          }
          .item-3{
            display: flex;justify-content: flex-end;
            margin-top: 16px;
            p:first-child{
              font-size: 24px;
              color: $black333333;
            }
            p:last-child{
              font-size: 28px;
              color: $black333333;
            }
          }
          .item-4{
            display: flex;justify-content: flex-end;
            margin-top: 24px;
            font-size: 26px;
            button{
              width: 150px;height: 54px;
              border-radius: 16px;
              border: 1.2px solid $black999999;
              color: $black999999;
            }
            .button-active{
              border: 1.2px solid $green22ac38;
              color: $green22ac38;
            }
            button + button{
              margin-left: 20px;
            }
          }
        }
      }
    }
  }
</style>



